<template>
	<view class="fatherBox">
		<view class="imgBox">
			<image src="../../static/image/mifeng.png" mode="widthFix"></image>
			<view class="beeBody"></view>
		</view>
		<view class="welText">WELCOME</view>
		<view class="inputBox">
			<view class="firstBox">
				<image src="../../static/image/me.png" mode="widthFix"></image>
				<input type="text"    @input="zhanghao">
			</view>
			<view class="secondBox">
				<image src="../../static/image/suo.png" mode="widthFix"></image>
				<input :type="typePW" placeholder="请输入密码" placeholder-style="color:#D4D5D5;font-size:30rpx" @input="password">
				<image @click="handelEye" src="../../static/image/eye.png" mode="widthFix" class="eye"></image>
			</view>
			<view class="loginBox" @click="loginButton">登录</view>
		</view>
	</view>
</template>

<script>
	import {
		getLogin
	} from '../../network/apiData.js'
	export default {
		data() {
			return {
				userValue: '',
				passwordValue: '',
				typePW: 'password'
			}
		},
		onShow() {
			// this.userValue = '';
			// this.passwordValue = '';
		},
		methods: {
			handelEye() {
				this.typePW = this.typePW === 'password' ? 'text' : 'password'
			},
			pushIndex() {
				uni.navigateTo({
					url: '../index/index'
				})
			},
			zhanghao(event) {
				this.userValue = event.detail.value
				console.log(this.userValue)
			},
			password(event) {
				this.passwordValue = event.detail.value
				console.log(this.passwordValue)
			},
			loginButton() {
				if (this.userValue === '' || this.passwordValue === '') {
					uni.showToast({
						title: '账号或密码不能为空',
						icon: 'none'
					})
				} else {
					getLogin(this.userValue, this.passwordValue).then(res => {
						if (res.data == true) {
							uni.setStorage({
								key: 'isLogin',
								data: res.data,
								success: function() {
									uni.navigateTo({
										url: '../index/index'
									})
									uni.showToast({
										title: '登录成功'
									})
								}
							});

						}else{
							uni.showToast({
								title: '账号或密码不正确',
								icon:'none'
							})
						}
						console.log(res)
					}).catch(err => {
						console.log(err)
					})
				}

			}
		}
	}
</script>

<style scoped lang="less">
	.fatherBox {
		position: relative;
		height: 100vh;
		background: url('data:image/jpg;base64,');

		.imgBox {
			position: absolute;
			left: 50%;
			top: 80rpx;
			transform: translateX(-50%);
			background-color: white;
			width: 400rpx;
			height: 400rpx;
			border-radius: 200rpx;

			image {
				position: absolute;
				top: 50%;
				left: 50%;
				transform: translate(-50%, -50%);
				width: 196rpx;

				z-index: 2;
			}

			.beeBody {
				position: absolute;
				top: 50%;
				left: 50%;
				transform: translate(-50%, -40%);
				width: 69rpx;
				height: 165rpx;
				background: linear-gradient(360deg, #FFF25F 0%, #FCCF00 1%, #FFD90D 100%);
				border-radius: 20rpx 20rpx 40rpx 40rpx;
			}
		}

		.welText {
			font-family: BankGothic Md BT;
			font-size: 46rpx;
			font-weight: 700;
			position: absolute;
			top: 44%;
			left: 50%;
			transform: translateX(-50%);
		}

		.inputBox {
			position: absolute;
			width: 690rpx;
			height: 467rpx;
			background-color: white;
			top: 70%;
			left: 50%;
			transform: translate(-50%, -50%);

			.firstBox {
				position: absolute;
				display: flex;
				align-items: center;
				left: 50%;
				top: 24%;
				transform: translate(-50%, -50%);
				width: 530rpx;
				height: 80rpx;
				line-height: 80rpx;
				border-bottom: 1rpx solid #DCDEE6;

				image {
					width: 60rpx;
					margin-right: 20rpx;
				}


			}

			.secondBox {
				position: absolute;
				display: flex;
				align-items: center;
				justify-content: space-between;
				left: 50%;
				top: 50%;
				transform: translate(-50%, -50%);
				width: 530rpx;
				height: 80rpx;
				line-height: 80rpx;
				border-bottom: 1rpx solid #DCDEE6;
				font-size: 30rpx;

				image {
					width: 60rpx;
					margin-right: 20rpx;
				}

				.eye {
					width: 40rpx;
				}
			}

			.loginBox {

				width: 530rpx;
				height: 76rpx;
				background: linear-gradient(360deg, #FFF25F 0%, #FCCF00 1%, #FFD90D 100%);
				position: absolute;
				top: 70%;
				left: 50%;
				transform: translateX(-50%);
				text-align: center;
				line-height: 76rpx;
				color: white;
				border-radius: 38rpx;

			}
		}

	}
</style>
